header {
    height: 100px;
    color: blue;
    background-color: #c464ea;
    padding: 30px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
}

header img {
    width: 50px;
}

header nav {
    width: 70%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}